html, body, html *, *, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

body.no-scroll {
  overflow: hidden;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}
.wrapper main {
  flex: 1;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}
button:disabled {
  cursor: unset;
}

li {
  list-style: none;
}

input, textarea {
  outline: none;
  border: none;
}

:root {
  --color-linear: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
  --color-1: #CA6AEB;
  --color-2: #19B5F1;
  --color-success: #00C566;
  --color-error: #FF4747;
  --color-warning: #FACC15;
  --color-grey-100: #18181B;
  --color-grey-50: #252532;
  --color-grey-1: #B0B0B2;
  --color-grey-2: #D1D1D1;
  --color-grey-white: #FFFFFF;
  --color-grey-linear: linear-gradient(90deg, #ffffff4d 0%, #ffffff00 100%);
}

::-webkit-scrollbar {
  width: 2px;
  background-color: transparent;
  border-radius: 100ch;
}

::-webkit-scrollbar-thumb {
  background-color: #fff;
  border-radius: 100ch;
  cursor: pointer;
}

::-webkit-scrollbar-track-piece {
  cursor: pointer;
  border-radius: 100ch;
  background-color: rgba(255, 255, 255, 0.15);
}

[class*=__container], .container {
  box-sizing: border-box;
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
  min-width: 360px;
}

body {
  background-color: #18181B;
  color: #FFF;
  background-image: url(../img/bg/body-top.webp), url(../img/bg/body-bottom.webp);
  background-blend-mode: normal;
  background-size: clamp(25.938rem, 11.241rem + 42.75vw, 44.375rem) auto, clamp(39.813rem, 21.18rem + 54.2vw, 63.188rem) auto;
  background-position: top right, bottom left;
}

* {
  font-family: "Chakra Petch", sans-serif;
}

.logo {
  display: flex;
  gap: 4px;
  align-items: center;
}

.text {
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
  color: var(--color-grey-2);
}

.title {
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  color: transparent;
  background: var(--color-linear);
  background-clip: text;
  text-transform: uppercase;
}

.subtitle {
  font-size: clamp(2rem, 1.203rem + 2.32vw, 3rem);
  font-weight: 600;
  letter-spacing: -0.5px;
}

.btn {
  position: relative;
  display: inline-flex;
  padding: 18px 38px;
  background-image: var(--color-linear);
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  text-align: center;
}
.btn::before {
  content: "";
  position: absolute;
  z-index: 2;
  inset: 0;
  background-image: var(--color-linear);
  transition: 0.3s ease;
  filter: blur(20px);
  opacity: 0;
}
.btn span, .btn i {
  position: relative;
  z-index: 3;
}
@media (any-hover: hover) {
  .btn:hover::before {
    opacity: 1;
  }
}
.btn_outline {
  border: 1px solid;
  border-image: var(--color-linear) 1;
  background: rgba(255, 255, 255, 0.059);
}
.btn_outline::before {
  background-image: var(--color-linear);
  filter: none;
}
@media (any-hover: hover) {
  .btn_outline:hover::before {
    opacity: 1;
  }
}

.social__list {
  display: flex;
  gap: 20px;
}
.social__item {
  border: 1px solid transparent;
  background: linear-gradient(var(--color-grey-100), var(--color-grey-100)) padding-box, var(--color-linear) border-box;
  width: 46px;
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: 0.1s ease;
}
.social__item a {
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .social__item:hover {
    border-width: 3px;
  }
  .social__item:hover a {
    color: transparent;
    background: var(--color-linear);
    background-clip: text;
  }
}

.content-footer {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.content-footer__title {
  font-size: 16px;
  font-weight: 600;
  line-height: 28px;
  margin-bottom: 16px;
}
.content-footer__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
}
.content-footer__item {
  display: flex;
  gap: 8px;
  align-items: center;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
  color: var(--color-grey-1);
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .content-footer__item:hover {
    color: var(--color-grey-white);
  }
}
@media (max-width: 980px) {
  .content-footer {
    gap: 64px 100px;
    justify-content: flex-start;
  }
}

.nav__list {
  position: relative;
  z-index: 151;
  display: flex;
  gap: clamp(0.938rem, -5.737rem + 12.56vw, 4rem);
  row-gap: 20px;
  align-items: center;
}
@media (max-width: 650px) {
  .nav__list {
    flex-direction: column;
    align-items: stretch;
  }
}
.nav__item {
  font-size: 16px;
  font-weight: 300;
  color: var(--color-grey-1);
  line-height: 28px;
}
.nav__item._rel {
  display: flex;
  align-items: center;
  position: relative;
  transition: 0.3s ease;
}
.nav__item a {
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .nav__item a:hover {
    color: #FFF;
  }
}
.nav__item a._no-page {
  color: var(--color-error);
  cursor: no-drop;
}
@media (max-width: 650px) {
  .nav__item {
    font-size: 20px;
    justify-content: space-between;
  }
}
.nav__item._opened .nav__subitems {
  transform: scaleY(1);
}
.nav__item._opened .nav__item-btn {
  transform: scaleY(-1);
}
.nav__item-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  content: "\e800";
  font-family: "fontello";
  font-size: 4px;
  margin-inline-start: 7px;
  margin-block-start: 4px;
  width: 12px;
  height: 12px;
  transition: 0.3s ease;
}
@media (max-width: 650px) {
  .nav__item-btn {
    font-size: 10px;
  }
}
.nav__subitems {
  background-image: var(--color-grey-linear);
  padding: 10px 15px;
  position: absolute;
  top: 100%;
  left: -10px;
  margin-top: 5px;
  width: 200px;
  z-index: 1;
  overflow: hidden;
  transition: 0.3s ease;
  transform: scaleY(0);
  transform-origin: top;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.nav__subitems::before {
  content: "";
  position: absolute;
  inset: 1px;
  background-color: var(--color-grey-50);
}
@media (max-width: 650px) {
  .nav__subitems {
    right: -10px;
    width: auto;
  }
}
.nav__subitem {
  position: relative;
  z-index: 2;
  height: max-content;
}
.nav__contact-us-btn {
  position: relative;
  z-index: 151;
  display: none;
  margin-top: 30px;
  width: 100%;
}
@media (max-width: 410px) {
  .nav__contact-us-btn {
    display: block;
  }
}
@media (max-width: 650px) {
  .nav {
    background-color: rgba(37, 37, 50, 0.9);
    position: fixed;
    overflow-x: auto;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 150;
    transform: translateX(-100%);
    transition: 0.3s ease;
    padding: 20px;
    padding-top: 110px;
  }
  .nav._opened {
    transform: translateX(0);
  }
}

.clients {
  display: flex;
  align-items: center;
  gap: 18px;
}
.clients__avatars {
  display: flex;
  align-items: center;
}
.clients__avatars img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  object-position: center;
  border-radius: 50%;
  border: 1px solid var(--color-grey-100);
  margin-left: -12px;
}
.clients__avatars img:first-child {
  margin-left: 0;
}
.clients__text {
  position: relative;
}
.clients__text ._strong {
  display: inline-block;
  position: relative;
  font-weight: 600;
  color: white;
}
.clients__text ._strong::before {
  display: block;
  line-height: 7px;
  content: url(../img/bg/underline.svg);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2px;
}

.video {
  position: relative;
  cursor: pointer;
  display: flex;
  width: 196px;
  height: 128px;
  overflow: hidden;
  z-index: 1;
}
.video img {
  display: flex;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: 0.3s ease;
}
.video::after {
  display: flex;
  font-family: fontello;
  content: "\e809";
  width: 28px;
  height: 28px;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  line-height: 1;
  border: 1px solid white;
  color: white;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (any-hover: hover) {
  .video:hover img {
    transform: scale(1.1);
  }
}
.video__popup {
  transition: 0.3s ease;
  position: fixed;
  cursor: default;
  inset: 0;
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 161;
  padding: 20px;
  background: rgba(0, 0, 0, 0.4235294118);
}
.video__popup .player-video {
  width: 100%;
  max-width: 800px;
  max-height: 400px;
  aspect-ratio: 1/2;
  align-self: auto;
  box-shadow: 0 0 40px 20px #000;
}
.video__popup._visibled {
  display: flex;
}

.player-video {
  height: clamp(15.875rem, 2.524rem + 38.84vw, 32.625rem);
  overflow: hidden;
  position: relative;
  align-self: stretch;
  border-radius: 12px;
}
.player-video__overlay {
  position: absolute;
  inset: 0;
  z-index: 3;
}
.player-video__overlay::before {
  position: absolute;
  transition: 0.3s ease;
  content: "";
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: clamp(2.5rem, -0.489rem + 8.7vw, 6.25rem);
  height: clamp(2.5rem, -0.489rem + 8.7vw, 6.25rem);
  background: url(../img/icons/play-btn.svg) no-repeat center/contain;
  opacity: 0.3;
}
.player-video__info {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: clamp(1.375rem, -0.12rem + 4.35vw, 3.25rem) clamp(1rem, 0.103rem + 2.61vw, 2.125rem) 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition: 0.3s ease;
}
.player-video__tag {
  background-color: rgba(255, 255, 255, 0.3);
  padding: 2px 10px;
  border-radius: 100ch;
  margin-bottom: 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
}
.player-video__title {
  font-size: clamp(1.25rem, 0.453rem + 2.32vw, 2.25rem);
  font-weight: 500;
}
.player-video__controlls {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transition: 0.3s ease;
